body {
  width: 300px;
  padding: 20px;
  font-family: "Segoe UI", system-ui, -apple-system, sans-serif;
  background: linear-gradient(135deg, #121212 0%, #0d0d0d 100%);
  color: #ffffff;
  margin: 0;
  position: relative;
  overflow: hidden;
}
body::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  background: radial-gradient(circle at center, rgba(33, 150, 243, 0.1) 0%, transparent 70%);
  z-index: -1;
  animation: rotate 20s linear infinite;
}

.hidden {
  display: none !important;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(33, 150, 243, 0.3);
  position: relative;
}
.header::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 30%;
  height: 2px;
  background: #2196F3;
  border-radius: 2px;
  transition: all 0.4s ease;
}
.header .brand {
  font-size: 22px;
  font-weight: 700;
  color: #2196F3;
  text-shadow: 0 0 10px rgba(33, 150, 243, 0.5);
  letter-spacing: -0.5px;
  animation: glow 3s infinite;
  display: flex;
  align-items: center;
  gap: 8px;
}
.header .brand-dot {
  display: block;
  width: 8px;
  height: 8px;
  background: #2196F3;
  border-radius: 50%;
  box-shadow: 0 0 8px #2196F3;
}
.header .header-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}
.header .header-controls .icon-button {
  background: rgba(30, 30, 30, 0.7);
  border: none;
  border-radius: 50%;
  padding: 6px;
  cursor: pointer;
  color: #b0b0b0;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  backdrop-filter: blur(4px);
}
.header .header-controls .icon-button:hover {
  background: #2196F3;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(33, 150, 243, 0.3);
}
.header .header-controls .icon-button svg {
  width: 18px;
  height: 18px;
}
.header .header-controls .switch-container {
  display: flex;
  align-items: center;
  gap: 8px;
}

#status {
  font-size: 14px;
  padding: 10px 15px;
  background: rgba(30, 30, 30, 0.7);
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 3px solid #2196F3;
  animation: fadeIn 0.5s ease;
  backdrop-filter: blur(4px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

#shortcut-container {
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  padding: 15px;
  background: rgba(30, 30, 30, 0.7);
  border-radius: 12px;
  backdrop-filter: blur(4px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.6s ease;
}
#shortcut-container #shortcut-key {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px 15px;
  min-width: 80px;
  border: 1px solid #2196F3;
  border-radius: 8px;
  font-family: "SF Mono", "Courier New", monospace;
  cursor: pointer;
  background: rgba(13, 71, 161, 0.2);
  color: #64b5f6;
  transition: all 0.3s ease;
  font-weight: 600;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}
#shortcut-container #shortcut-key::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(33, 150, 243, 0.2), transparent);
  transform: translateX(-100%);
}
#shortcut-container #shortcut-key:hover {
  background: rgba(33, 150, 243, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(33, 150, 243, 0.3);
}
#shortcut-container #shortcut-key:hover::before {
  animation: shine 1.5s infinite;
}
#shortcut-container #shortcut-key.listening {
  background: #2196F3;
  color: white;
  animation: pulse 1.5s infinite;
}

@keyframes shine {
  100% {
    transform: translateX(100%);
  }
}
#intensity-container {
  background: rgba(30, 30, 30, 0.7);
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  backdrop-filter: blur(4px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.7s ease;
}
#intensity-container label {
  display: block;
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
}
#intensity-container label span {
  color: #64b5f6;
}
#intensity-container #filter-intensity {
  width: 100%;
  margin: 12px 0;
  -webkit-appearance: none;
  appearance: none;
  height: 6px;
  background: linear-gradient(90deg, #0d47a1, #2196F3);
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}
#intensity-container #filter-intensity::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid #2196F3;
  box-shadow: 0 0 10px rgba(33, 150, 243, 0.7);
}
#intensity-container #filter-intensity::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 0 15px #2196F3;
}
#intensity-container #intensity-value {
  font-size: 13px;
  text-align: center;
  color: #64b5f6;
  font-weight: 600;
  margin-top: 10px;
  display: block;
}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 26px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #1e1e1e;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border-radius: 34px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
}
.switch .slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border-radius: 50%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}
.switch input:checked + .slider {
  background: linear-gradient(90deg, #0d47a1, #2196F3);
}
.switch input:checked + .slider:before {
  transform: translateX(24px);
  background-color: white;
  box-shadow: 0 0 10px rgba(33, 150, 243, 0.8);
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(33, 150, 243, 0.2);
  font-size: 12px;
  animation: fadeIn 1s ease;
}
.footer a {
  color: #b0b0b0;
  text-decoration: none;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  border-radius: 6px;
}
.footer a:hover {
  color: #2196F3;
  background: rgba(33, 150, 243, 0.1);
  transform: translateY(-2px);
}
.footer a:hover svg {
  fill: #2196F3;
}
.footer a svg {
  width: 14px;
  height: 14px;
  fill: #b0b0b0;
  transition: all 0.3s ease;
}

.view {
  display: none;
  animation: fadeIn 0.4s ease;
}
.view.active {
  display: block;
}

.back-button {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #64b5f6;
  background: none;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
  margin-bottom: 15px;
  border-radius: 8px;
}
.back-button:hover {
  color: #2196F3;
  background: rgba(33, 150, 243, 0.1);
  transform: translateX(-5px);
}
.back-button:hover svg {
  transform: translateX(-3px);
